"use client";

import { useEffect, useState } from "react";
import { fetchSP500Overview, fetchTopPerformers, fetchBottomPerformers } from "@/lib/api";
import StockDashboard from "@/components/StockDashboard";
import StockTable from "@/components/StockTable";
import { useTicker } from "@/context/TickerContext";
import VolumeChart from "@/components/VolumeChart";

export default function Home() {
    const [sp500, setSP500] = useState([]);
    const [topStocks, setTopStocks] = useState([]);
    const [bottomStocks, setBottomStocks] = useState([]);
    const [stockData, setStockData] = useState([]);
    const [tickerDetails, setTickerDetails] = useState<{ ticker: string; company_name: string; sector: string } | null>(null);
    const { selectedTicker } = useTicker();

    useEffect(() => {
        async function loadData() {
            try {
                const topStocksData = await fetchTopPerformers();
                setTopStocks(topStocksData);

                const bottomStocksData = await fetchBottomPerformers();
                setBottomStocks(bottomStocksData);

                //const sp500Data = await fetchSP500Overview();
                //setSP500(sp500Data);

            } catch (error) {
                console.error("Error fetching data:", error);
            }
        }
        loadData();
    }, []);

    useEffect(() => {
        async function fetchStockData() {
            if (!selectedTicker) return;
            try {
                // Fetch stock data
                const res = await fetch(`http://127.0.0.1:8000/sp500/stock/${selectedTicker}`);
                const data = await res.json();
                setStockData(Array.isArray(data) ? data : []);

                // Fetch ticker details (company name & sector)
                const tickersRes = await fetch(`http://127.0.0.1:8000/sp500/tickers`);
                const tickersData = await tickersRes.json();

                const tickerInfo = tickersData.find((t: any) => t.ticker === selectedTicker);

                if (tickerInfo) {
                    setTickerDetails({
                        ticker: tickerInfo.ticker,
                        company_name: tickerInfo.company_name,
                        sector: tickerInfo.sector
                    });
                } else {
                    setTickerDetails(null);
                }
            } catch (error) {
                console.error(`Error fetching data for ${selectedTicker}:`, error);
            }
        }

        fetchStockData();
    }, [selectedTicker]); // Runs only when the ticker changes


    return (
        <div>
            <StockDashboard ticker={selectedTicker} stockData={stockData} tickerDetails={tickerDetails} />
            <div className="flex">
                <div className="w-1/2 h-max grid grid-cols-2 gap-6 mt-6">
                    <StockTable title="Top Performers" stocks={topStocks} />
                    <StockTable title="Bottom Performers" stocks={bottomStocks} />
                </div>
                <div className="w-1/2">
                    {stockData.length > 0 && stockData.some(stock => stock.Volume !== undefined) ? (
                        <VolumeChart data={stockData.map(({ Ticker, Date, Volume }) => ({ Ticker, Date, Volume }))} />
                    ) : (
                        <div className="p-4 text-white">No volume data available</div>
                    )}
                </div>
            </div>
        </div>
    );
}
